/* restart rules are from my board game, but they might come in handy for other buttons. clear, etc? -Geoff */

.restart-button {
  margin-top: 20px;
  text-align: center;
}

.restart:hover {
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
}

.restart:focus {
  outline: none;
}

.restart:active {
  box-shadow: 0 1px #424242;
  -webkit-transform: translateY(1px);
          transform: translateY(1px);
}

.restart { /* Safari */
  transition-duration: 0.4s;
  background-color: #424242;
  color: #fffde7;
  border: none;
  font-size: 24px;
  line-height: 48px;
  height: 48px;
  width: 40%;
  text-align: center;
  border-radius: 4px;
}
.App {
  text-align: center;
  height: 100%;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "header header header"
                        "left main right"
                        "footer footer footer";
  grid-template-rows: 1fr 3fr 1fr;
  grid-template-columns: 0.5fr 2.5fr 1fr;
  background-color: #0B172A;
  color: white;
  font-family: 'Poppins', sans-serif;
}

.cell-main {
  grid-area: main;
  padding: 1%;
  border-radius:25px;
  border: 3px solid;
  border-color: #BC4123;
}

.cell-left {
  border-radius: 25px;
  border: 2px solid rgb(19, 255, 255);
  grid-area: left;
  margin: 5%;
}

.cell-right {
  border-radius: 25px;
  border: 2px solid rgb(19, 255, 255);
  grid-area: right;
  margin: 5%;
}

.App-header {
  grid-area: header;
  background-color: #0B172A;
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

