.card {
  color: black;
  background-color: #ffffff;
  transition: 0.3s;
  margin-top: 3%;
}
.card:hover {
  -webkit-transform: scale(.95);
          transform: scale(.95); cursor: grab;
}
.card:active { cursor: grabbing;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.delete-button {
  position: absolute;
  right: -10px;
  visibility: hidden;
  transition: 0.3s;
  background: transparent;
  border: 0px;
}
.delete-course {
  position: absolute;
  right: -10px;
  visibility: hidden;
  transition: 0.3s;
  background: transparent;
  border: 0px;
  size: 0.01px;
}
.card:hover .delete-button {
  right: 0px;
  -webkit-transform: scale(1.75);
          transform: scale(1.75);
  visibility: visible;
}
.delete-button:hover {
  color: crimson;
}
.card:hover .delete-course {
  right: 0px;
  -webkit-transform: scale(1.3);
          transform: scale(1.3);
  visibility: visible;
}
.delete-course:hover {
  color: crimson;
}
.select-button-on {
  position: absolute;
  color: rgb(46, 196, 46);
  visibility: visible;
  transition: 0.3s;
  background: transparent;
  border: 0px;
  size: 2px;
  left: 2px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}
.select-button-off {
  position: absolute;
  left: -10px;
  visibility: hidden;
  transition: 0.3s;
  background: transparent;
  border: 0px;
  size: 2px;
}
.card:hover .select-button-off {
  left: 2px;
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
  visibility: visible;
}
.select-button-off:hover {
  color: rgb(46, 196, 46);
}

.edit-button {
  position: absolute;
  right: -10px;
  visibility: hidden;
  transition: 0.3s;
  background: transparent;
  border: 0px;
}
.card:hover .edit-button {
  right: 0px;
  -webkit-transform: scale(1.75);
          transform: scale(1.75);
  visibility: visible;
}
.edit-button:hover {
  color: rgb(64, 102, 7);
}

