.board-row {
    margin: 30px 5px 50px 5px;
}
.course {
    border-radius:25px;
    border: 3px solid;
    border-color: #fff;
    background: #fff;
    color: black;
    margin: 5%;
    margin-left: 10%;
    margin-right: 10%;
    transition: 0.3s;
}

.add-button {
    margin: 5%;
    transition: 0.3s;
    background: transparent;
    border: 0px;
}

.course:hover {
    -webkit-transform: scale(.95);
            transform: scale(.95); cursor: grab;
}
.course:active{ cursor: grabbing;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
