.home {
  /* background: linear-gradient(#000428, #004e92); */
  /* background: linear-gradient(#141e30, #243b55); */
  background: linear-gradient(#0b486b, #f56217);
  color: white;
  font-family: 'Poppins', sans-serif;
  text-align: center;
  min-height: 100vh;
}

.centered {
    position: absolute;
    top: 50%;
    left: 50%;
    margin-right: -50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 10%;
}

.home h1 {
    font-size: 70pt;
}

.home button {
    background-color: #f5611779;
    border-radius: 20px;
    border: 0px;
    margin: 10%;
    padding-top: 2%;
    padding-bottom: 2%;
    padding-left: 10%;
    padding-right: 10%;
    transition: 0.3s;
}
.home button:hover {
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
    background-color: #f56117f1;
}
